#about{
    padding: 150px 0;
    color: #ababab;
}
.row{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.about-col-1{
    flex-basis: 35%;
}

.about-col-1 img{
    width: 85%;
    border-radius: 25px;
}

.about-col-2{
    flex-basis: 65%;
}

.sub-title{
    font-size: 60px;
    font-weight: 600;
    color: #f0f0f0;
    margin-bottom: 20px;     
}

.tab-titles{
    display: flex;
    margin: 20px 0 10px;
}

.tab-links{
    margin-right: 50px;
    font-size: 19px;
    font-weight: 500;
    cursor: pointer;
    position: relative;
    color: #f0f0f0;
}

.about-col-2 .text{
    font-size: 18px;
    padding: 5px 0;
    color: #ababab ;
    margin-bottom: 25px;
}

.tab-links::after{
    content: '';
    width: 0;
    height: 3px;
    background: #ff004f;
    position: absolute;
    left: 0;
    bottom: -8px;
    transition: 0.5s;
}

.tab-links.active-link::after{
    width: 50%;
}

.tab-contents ul li {
    display: flex;
    font-size: 18px;
    margin: 15px 0px; 
    word-spacing: 0em;
}

.tab-contents ul li span{
    color: #b54769;
    font-weight: 500;
    transition: color 0.5s;
}

.tab-contents ul li span:hover{
    color: #f0f0f0;
}

.tab-contents{
    display: none;
}

.tab-contents.active-tab{
    display: block;
}